<template>
  <div class="board pt-5">
    <div class="container pt-5">
      <div class="row edit">
        <div class="col-lg">
          <div class="row">
            <div class="col-lg-auto mb-3 me-4">
              <div class="d-flex align-items-center">
                <label for="" class="me-2">專案名稱：</label>
                <input type="text" name="" id="" v-model.trim="tempItem.name">
              </div>
            </div>
            <div class="col-lg-auto d-flex align-items-center mb-3 me-4">
              <div class="d-flex align-items-center">
                <label for="type" class="me-2">類別：</label>
                <label for="business" class="me-4"><input type="radio" name="type" id="business" class="me-2" value="Business" v-model="tempItem.type">企業</label>
                <label for="shop"><input type="radio" name="type" id="shop" class="me-2" value="Shop" v-model="tempItem.type">電商</label>
              </div>
            </div>
            <div class="col-lg-auto mb-3 me-4">
              <div class="d-flex align-items-center">
                <label for="" class="me-2">專案連結：</label>
                <input type="text" name="" id="" style="width: 300px;" v-model.trim="tempItem.link">
              </div>
            </div>
            <div class="col-lg-auto mb-3 me-4">
              <div class="d-flex align-items-center">
                <label for="" class="me-2">圖片：</label>
                <input type="text" name="" id="" v-model.trim="tempItem.img">
              </div>
            </div>
            <div class="col-lg-auto mb-3 me-4">
              <div class="d-flex align-items-center">
                <label for="" class="me-2">說明：</label>
                <input type="text" name="" id="" v-model.trim="tempItem.info">
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-auto d-flex align-items-center justify-content-end">
          <button class="me-4 btn btn-primary px-4" @click.prevent="sendData">送出</button>
          <button class="btn btn-primary px-4" @click.prevent="cancelData">取消</button>
        </div>
      </div>
      <table class="table mt-4 bg-white">
        <thead>
          <tr>
            <th>專案名稱</th>
            <th>類別</th>
            <th>專案連結</th>
            <th>圖片</th>
            <th>說明</th>
            <th>編輯</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in list" :key="item.name">
            <td>{{ item.name }}</td>
            <td>{{ item.type}}</td>
            <td>{{ item.link}}</td>
            <td>{{ item.img}}</td>
            <td>{{ item.info}}</td>
            <td>
              <div class="btn-group">
                <button class="btn btn-outline-primary btn-sm" @click.prevent="editData(index)">編輯</button>
                <button class="btn btn-outline-danger btn-sm" @click.prevent="deleteData(index)">刪除</button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tempItem: {
        name: '',
        type: '',
        link: '',
        img: '',
        info: ''
      },
      list: [],
      editIndex: null
    }
  },
  methods: {
    getList () {
      const url = 'http://localhost:9090/data'
      this.$http.get(url)
        .then((res) => {
          this.list = res.data.sort(function (a, b) { return a.info < b.info ? 1 : -1 })
        })
    },
    sendData () {
      const { name, type, link, img, info } = this.tempItem
      if (!name || !type || !link || !img || !info) return
      const id = this.list[this.editIndex].id
      let url = 'http://localhost:9090/data'
      let typeItem = 'post'
      if (this.editIndex != null) {
        url = 'http://localhost:9090/data/' + id
        typeItem = 'put'
      }
      this.$http[typeItem](url, this.tempItem)
        .then(() => {
          this.getList()
          this.cancelData()
        })
    },
    cancelData () {
      this.editIndex = null
      this.tempItem = {
        name: '',
        type: '',
        link: '',
        img: '',
        info: ''
      }
    },
    deleteData (index) {
      const delItem = this.list[index]
      if (confirm(`是否刪除 ${delItem.name} ?`)) {
        const url = 'http://localhost:9090/data/' + delItem.id
        this.$http.delete(url)
          .then(() => {
            this.list.splice(index, 1)
            this.getList()
          })
      }
    },
    editData (index) {
      const { name, type, link, img, info } = this.list[index]
      this.editIndex = index
      this.tempItem = { name, type, link, img, info }
    }
  },
  mounted () {
    this.getList()
  }
}
</script>
<style>
thead{
  background: #ddd;
}
.edit .btn{
  color: #fff !important;
}
.btn-outline-primary:hover{
  color: #fff;
}
</style>
